import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';

// @material-ui/icons
import AccessTime from "@material-ui/icons/AccessTime";
import Delete from "@material-ui/icons/Delete";
import Save from "@material-ui/icons/Save";
import Scale from "@material-ui/icons/LinearScale";
import Edit from "@material-ui/icons/Edit";
import PlayCircleOutline from "@material-ui/icons/PlayCircleOutline";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";

// exp components
import NBrootsPagination from "components/@NBrootsPagination/NBrootsPagination.js";
import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsDialogPlayer from "components/@NBrootsDialogPlayer/NBrootsDialogPlayer.js";
import NBrootsMsgBox from "components/@NBrootsMsgBox/NBrootsMsgBox.js";
import NBrootsConfirm from "components/@NBrootsConfirm/NBrootsConfirm.js";

import Service from "utils/service/service.js"

import gUid from "utils/functions/gUid.js"
import verifyEmail from "utils/validations/verifyEmail"
import verifyLength from "utils/validations/verifyLength"

import { BASE_URL } from 'variables/config.js';
import styles from "assets/jss/material-dashboard-pro-react/views/VedioList/vediosListStyle.js";

// メモリ中企業リスト
import { corpsInfo } from 'variables/runtimeInfos.js';
import { PAGINATION_PREV_NAME,PAGINATION_NEXT_NAME } from 'variables/config.js';


export default function VediosList() {

  const useStyles = makeStyles(styles);

  const classes = useStyles();

  //進捗画面
  const [isLoading, setIsLoading] = React.useState(false);
  //player画面用情報          playerInfo  : [表示FLG、VedioSrc]    
  const [playerInfo, setPlayerInfo] = React.useState([false, '']);
  //Msg画面用情報             msgInfo     : [表示FLG, Msg, Type:"info","success","warning","danger","primary","rose"]    
  const [msgInfo, setMsgInfo] = React.useState([false, '', 'info']);
  //Confirm画面用情報         confirmInfo : [表示FLG, Msg, returnVal ]    
  const [confirmInfo, setConfirmInfo] = React.useState([false, '', { "opt": '', "id": '', "result": 0 }]);

  var tSearch = {
    crop: ''
  }
  const [search, setSearch] = React.useState(tSearch);

  //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
  const [pageInfos, setPageInfos] = React.useState([0, 10, 1]);
  // // 検索条件用情報
  // var tSearchInfo = {
  //   // [検索条件1,検索条件2]
  //   searchConds : ["",""],
  //   // ソート用
  //   sort        : ["orderBy","sortType"],
  //   // [記録数、ページサイズ,当ページ番号]
  //   pageInfos   : [0, 10, 1]
  // }
  // const [searchInfo, setSearchInfo] = React.useState(tSearchInfo);

  //明細情報
  const [lstContent, setLstContent] = React.useState([]);

  //初期化処理
  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {

    //進捗画面
    setIsLoading(true);
    //情報のロード
    formatLstData();

  }, []);    //[] 忘れないよう…　初期化

  //APIJSON　→　画面用JSON（FLGやERRMSGなど）
  // const formatLstData = async () => {
  const formatLstData = () => {


    var url = BASE_URL + "/api/vedio_list.json";

    var data = {

    };

    Service(url, data, "GET").then(resp => {
      console.log(resp);
      // 正常系
      if (resp.status === 0) {

        var info = resp.infos;

        //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
        setPageInfos([info.count, info.pageSize, info.pageNo]);

        //Data情報の処理
        var lst = info.dataRows;
        lst.map(item => {
          return {
            ...item,
            //追加属性
            isEdit: false,
            chkResultName: false,
          }
        });

        setLstContent(lst);
      } else {
        //その他APIエラー      
      }

      setIsLoading(false);
    }).catch(resp => {
      //接続エラーなど
      // console.log(resp);

      setIsLoading(false);

      setMsgInfo([true, resp, 'danger']);

    });

  };

  //検索条件部
  //企業変更の場合
  const handleChangeSearchStart = (e) => {
    console.log(e);
    let t = search;
    
    //選択情報をSTATEに保存
    if(e.target.name === 'corpSelect')
      t.crop = e.target.value;
    // else if(e.target.name === 'groupSelect')
    //   t.group = e.target.value;
    setSearch(t);

    //検索ロジックを行う
    setIsLoading(true);

    setTimeout(() => {

      //情報のロード
      formatLstData(t);

      setIsLoading(false);

    }, 1500);

  }


  //改ページ処理
  // pageInfos   : [記録数、ページサイズ,当ページ番号]
  const handleChangePageStart = (page) => {

    if (page.disabled) return;
    if (page.text === pageInfos[2]) return;

    setIsLoading(true);

    setTimeout(() => {

      if (page.text === PAGINATION_PREV_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] - 1]);
      } else if (page.text === PAGINATION_NEXT_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] + 1]);
      } else {
        setPageInfos([pageInfos[0], pageInfos[1], page.text]);
      }

      setIsLoading(false);

    }, 500);
  }

  //画面動作 EDIT
  const handleClickStartEdit = (obj) => {

    var index = lstContent.indexOf(obj);

    if (obj.isEdit) {
      obj.isEdit = false;
    } else {
      obj.isEdit = true;
    }
    lstContent[index] = obj;

    //！！！　Array　展開方式 ！！！
    setLstContent([...lstContent]);

  };

  //画面入力チェック結果
  const setChkResult = (obj, propname, inState) => {

    var index = lstContent.indexOf(obj);

    lstContent[index][propname] = inState;

    //！！！　Array　展開方式 ！！！
    setLstContent([...lstContent]);

  };

  //画面動作 Edit On Change
  const handleChangeStartEdit = (obj, propname, inVal) => {

    var index = lstContent.indexOf(obj);

    lstContent[index][propname] = inVal;
    //！！！　Array　展開方式 ！！！
    setLstContent([...lstContent]);

  };

  //画面動作 SAVE
  const handleClickStartSave = (obj) => {

    var index = lstContent.indexOf(obj);

    setIsLoading(true);

    var url = BASE_URL + "/api/ok_resp.json";

    //デモ用
    if (obj.id === 2) {
      url = BASE_URL + "/api/ng_resp_10001.json";
    } else if (obj.id === 3) {
      url = BASE_URL + "/api/ng_resp_10002.json";
    } else if (obj.id === 4) {
      url = BASE_URL + "/api/ng_resp_10003.json";
    }

    console.log(url);

    var data = obj;

    //更新処理
    Service(url, data, "GET").then(resp => {
      // 正常系
      if (resp.status === 0) {

        obj.isEdit = false;
        lstContent[index] = obj;

        //TODO　メッセージ出す
        //console.log(resp.msg);
        setMsgInfo([true, resp.msg, 'success']);

      } else {
        //TODO　メッセージ出す
        //console.log(resp.msg);
        setMsgInfo([true, resp.msg, 'danger']);

      }
      //TODO　もう一回一覧を取得する。
      //！！！　Array　展開方式 ！！！
      setLstContent([...lstContent]);
      //進捗画面を閉じる
      setIsLoading(false);

    }).catch(resp => {
      //接続エラーなど
      // console.log(resp);
      setMsgInfo([true, resp, 1500, 'danger']);
      //進捗画面を閉じる
      setIsLoading(false);

    });

  };

  //画面動作 ADD
  const handleClickStartAdd = () => {
    var item =
    {
      id: gUid(),
      src: "https://akechi1980.github.io/NBroots/img/VedioMarket/1.mp4",
      poster: "https://akechi1980.github.io/NBroots/img/VedioMarket/1.png",
      name: "1.mp4",
      detail: "映像内容、説明する時使う内容をここに記入する映像内容、説明する時使う内容をここに記入する映像内容、説明する時使う内容をここに記入する映像内容、説明する時使う内容をここに記入する",
      price: "110",
      size: "110M",
      length: "20S",
      //追加属性
      isEdit: false,
      chkResultName: '',
    };

    setIsLoading(true);

    setTimeout(() => {

      //！！！　Array　展開方式 ！！！
      setLstContent([...lstContent, item]);

      setIsLoading(false);

    }, 1000);

  };

  //画面動作 PLAY
  const handleClickStartPlay = (obj) => {

    //Player画面を開く
    setPlayerInfo([true, obj.src]);

  };

  //画面動作 DEL
  const handleClickStartDel = (obj) => {
    var index = lstContent.indexOf(obj);
    setConfirmInfo([true, "削除、よろしいでしょうか？",
      {
        "opt": "del",
        "id": index,
        "result": 0
      }
    ]);
  };

  //確認メッセージ後の処理
  React.useEffect(() => {
    
    let subscribed = true;

    //削除処理
    function doDel() {

      if (subscribed !== true) return;
      
      setIsLoading(true);
      console.log("Del : " + confirmInfo[2].id);
      setTimeout(() => {

        formatLstData();
        setMsgInfo([true, '削除成功しました。', 'success']);

      }, 1000);
    }

    //起動確認結果
    if (confirmInfo[2].opt === "del" && confirmInfo[2].result === 1) {
      doDel();
    }

    // Specify how to clean up after this effect:
    return function cleanup() {
      console.log("cleanup");
    };
  }, [confirmInfo]);

  return (
    <Box>
      <Grid container spacing={1}>
        {/* 検索部 */}
        <Grid item xs={6} sm={6} className={classes.searchBox}>
          <FormControl style={{ width: "40%" }} className={classes.selectFormControl}>
            <InputLabel htmlFor="corpSelect" className={classes.selectLabel}>
              企業
            </InputLabel>
            <Select
              MenuProps={{ className: classes.selectMenu }}
              classes={{ select: classes.select }}
              value={search.crop}
              onChange={(e) => handleChangeSearchStart(e)}
              inputProps={{ name: "corpSelect"}}>
              <MenuItem disabled classes={{ root: classes.selectMenuItem }}>
                企業を選択
              </MenuItem>
              {corpsInfo ? corpsInfo.lst.map((item,key) =>
                <MenuItem classes={{ root: classes.selectMenuItem, selected: classes.selectMenuItemSelected }} value={item.id} key={key}>
                  {item.corpname}
                </MenuItem> 
              ) : null}

            </Select>
          </FormControl>
        </Grid>
        {/* 操作ボタン部 */}
        <Grid item xs={6} sm={6} className={classes.optBox}>
          <Button color="warning" onClick={() => handleClickStartAdd()}>新しい映像</Button>
        </Grid>
        {/* ページジャンプ */}
        <Grid item xs={12} className={classes.pageJumpBox}>
          {/* [記録数、ページサイズ,当ページ番号] */}
          <NBrootsPagination
            count={pageInfos[0]}
            pagesize={pageInfos[1]}
            current={pageInfos[2]}
            onClick={handleChangePageStart}
            color="warning"
          />
        </Grid>
      </Grid>

      {/* ScrollBar表示部分 */}
      <Box xs={12} className={classes.gidBox}>
        <GridContainer>
          {lstContent.map((item, idx) =>
            <GridItem xs={12} sm={6} md={3} key={idx}>
              <Card product className={classes.cardHover}>
                {/* 画像部 */}
                <CardHeader image className={classes.cardHeaderHover}>
                  <a href="#pablo" onClick={e => e.preventDefault()}>
                    <img src={item.poster} alt="..." />
                  </a>
                </CardHeader>
                {/* 操作部 */}
                <CardBody className={classes.cardBody}>
                  <div className={classes.cardHoverUnder}>
                    <Tooltip id="tooltip-top" title="プレイ" placement="bottom"
                      classes={{ tooltip: classes.tooltip }}>
                      <Button color="transparent" simple justIcon>
                        <PlayCircleOutline className={classes.underChartIcons}
                          onClick={() => handleClickStartPlay(item)} />
                      </Button>
                    </Tooltip>
                    {item.isEdit ?
                      <Tooltip id="tooltip-top" title="保存" placement="bottom"
                        classes={{ tooltip: classes.tooltip }}>
                        <Button color="success" simple justIcon>
                          <Save className={classes.underChartIcons}
                            onClick={() => handleClickStartSave(item)} />
                        </Button>
                      </Tooltip>
                      :
                      <Tooltip id="tooltip-top" title="編集" placement="bottom"
                        classes={{ tooltip: classes.tooltip }}>
                        <Button color="success" simple justIcon>
                          <Edit className={classes.underChartIcons}
                            onClick={() => handleClickStartEdit(item)} />
                        </Button>
                      </Tooltip>
                    }
                    <Tooltip id="tooltip-top" title="削除" placement="bottom"
                      classes={{ tooltip: classes.tooltip }}>
                      <Button color="danger" simple justIcon>
                        <Delete className={classes.underChartIcons}
                          onClick={() => handleClickStartDel(item)} />
                      </Button>
                    </Tooltip>
                  </div>
                  {item.isEdit ?
                    <CustomInput id="name"
                      success={item.chkResultName === "success"}      // chkResult + Json.prop
                      error={item.chkResultName === "error"}
                      formControlProps={{
                        className: classes.inputName,
                        fullWidth: true
                      }}
                      value={item.name}                               //set
                      inputProps={{
                        defaultValue: item.name,                      //set
                        onChange: event => {
                          if (verifyEmail(event.target.value) && verifyLength(event.target.value, 20)) {
                            setChkResult(item, "chkResultName", "success");
                          } else {
                            setChkResult(item, "chkResultName", "error");
                          }
                          handleChangeStartEdit(item, "name", event.target.value);
                        },
                      }}
                    />
                    :
                    <h4 className={classes.cardProductTitle}>
                      {item.name}
                    </h4>
                  }
                </CardBody>
                {/* foot部 */}
                <CardFooter product>
                  <div className={classes.price}>
                    <Scale /><p>{item.size}</p>
                  </div>
                  <div className={classes.price}>
                    <AccessTime /><p>{item.length}</p>
                  </div>
                </CardFooter>
              </Card>
            </GridItem>
          )}
        </GridContainer>
      </Box>
      {/* 共通ダイアログ画面 */}
      <NBrootsConfirm info={confirmInfo} setInfo={setConfirmInfo} />
      <NBrootsDialogPlayer open={playerInfo[0]} src={playerInfo[1]} onClose={() => setPlayerInfo([false, ''])} />
      <NBrootsDialogProcess open={isLoading} color="warning" />
      <NBrootsMsgBox open={msgInfo[0]} message={msgInfo[1]} color={msgInfo[2]} close setMessage={setMsgInfo} />
    </Box>
  );
}
