/* eslint-disable */
import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';

// @material-ui/icons
import Dvr from "@material-ui/icons/Dvr";
import Delete from "@material-ui/icons/Delete";
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';

// core components
import Button from "components/CustomButtons/Button.js";

// exp components
import NBrootsPagination from "components/@NBrootsPagination/NBrootsPagination.js";
import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsMsgBox from "components/@NBrootsMsgBox/NBrootsMsgBox.js";
import NBrootsConfirm from "components/@NBrootsConfirm/NBrootsConfirm.js";


import UserListAddPopup from "views/CommonPopup/UserListAddPopup.js";
import UserListDetailPopup from "views/CommonPopup/UserListDetailPopup.js";

import Service from "utils/service/service.js"

import { BASE_URL } from 'variables/config.js';

import styles from "assets/jss/material-dashboard-pro-react/views/MngCrop/UserList/userListStyle.js";
import { PAGINATION_PREV_NAME,PAGINATION_NEXT_NAME } from 'variables/config.js';


export default function UserList() {

  const useStyles = makeStyles(styles);

  const classes = useStyles();

  //進捗画面
  const [isLoading, setIsLoading] = React.useState(false);
  //Msg画面用情報             msgInfo     : [表示FLG, Msg, Type:"info","success","warning","danger","primary","rose"]    
  const [msgInfo, setMsgInfo] = React.useState([false, '', 'info']);
  //Confirm画面用情報         confirmInfo : [表示FLG, Msg, returnVal ]    
  const [confirmInfo, setConfirmInfo] = React.useState([false, '', { "opt": '', "id": '', "result": 0 }]);
  //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
  const [pageInfos, setPageInfos] = React.useState([0, 10, 1]);
  //明細情報
  const [lstContent, setLstContent] = React.useState([]);


  var tUserListAddInfo = [
    //当画面表示FLG    
    false,
    //当画面編集情報       
    {
      "corp": "",
      "email": "",
      "username": "",
      "furikana": "",
      "password": "",
      "isadmin": 0
    },
    //IsAfterOK
    false
  ]
  const [userListAddInfo, setUserListAddInfo] = React.useState(tUserListAddInfo);


  var tUserListDetailInfo = [
    //当画面表示FLG    
    false,
    //当画面編集情報       
    {
      "id": ""
    },
    //IsAfterOK
    false
  ]
  const [userListDetailInfo, setUserListDetailInfo] = React.useState(tUserListDetailInfo);

  //初期化処理
  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {

    //進捗画面
    setIsLoading(true);
    //情報のロード
    formatLstData();

  }, []);    //[] 忘れないよう…　初期化

  //APIJSON　→　画面用JSON（FLGやERRMSGなど）
  // const formatLstData = async () => {
  const formatLstData = () => {
    var url = BASE_URL + "/api/user_list_corp.json";
    var data = {

    };

    Service(url, data, "GET").then(resp => {
      console.log(resp);
      // 正常系
      if (resp.status === 0) {

        var info = resp.infos;

        //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
        setPageInfos([info.count, info.pageSize, info.pageNo]);

        //Data情報の処理
        var lst = info.dataRows;
        lst.map(item => {
          return {
            ...item,
            //追加属性
            isEdit: false,
            chkResultName: false,
          }
        });

        setLstContent(lst);
      } else {
        //その他APIエラー      
      }

      setIsLoading(false);
    }).catch(resp => {
      //接続エラーなど
      // console.log(resp);

      setIsLoading(false);

      setMsgInfo([true, resp, 'danger']);

    });

  };


  //追加ボタン押す
  const handleClickStartAdd = () => {
    userListAddInfo[0] = true;
    userListAddInfo[2] = false;
    setUserListAddInfo([...userListAddInfo]);
  }

  //Popup画面後の処理
  React.useEffect(() => {
    //追加処理
    function doAdd(obj) {
      setIsLoading(true);
      console.log("Add : " + obj);
      setTimeout(() => {

        setMsgInfo([true, '追加成功しました。', 'success']);
        setIsLoading(false);

      }, 1000);
    }

    if (userListAddInfo[2] === true)
      doAdd(userListAddInfo);

  }, [userListAddInfo]);

  //編集ボタン押す　Popup
  const handleClickStartEdit = () => {
    userListDetailInfo[0] = true;
    userListDetailInfo[2] = false;
    setUserListDetailInfo([...userListDetailInfo]);

  }

  //Popup画面後の処理
  React.useEffect(() => {
    //追加処理
    function doAdd(obj) {
      setIsLoading(true);
      console.log("Update : " + obj);
      setTimeout(() => {

        setMsgInfo([true, '更新成功しました。', 'success']);
        setIsLoading(false);

      }, 1000);
    }

    if (userListDetailInfo[2] === true)
      doAdd(userListDetailInfo);

  }, [userListDetailInfo]);


  //改ページ処理
  // pageInfos   : [記録数、ページサイズ,当ページ番号]
  const handleChangePageStart = (page) => {

    if (page.disabled) return;
    if (page.text === pageInfos[2]) return;

    setIsLoading(true);

    setTimeout(() => {

      if (page.text === PAGINATION_PREV_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] - 1]);
      } else if (page.text === PAGINATION_NEXT_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] + 1]);
      } else {
        setPageInfos([pageInfos[0], pageInfos[1], page.text]);
      }

      setIsLoading(false);

    }, 500);
  }

  //画面動作 DEL
  const handleClickStartDel = (obj) => {

    var index = lstContent.indexOf(obj);

    setConfirmInfo([true, "削除、よろしいでしょうか？",
      {
        "opt": "del",
        "id": index,
        "result": 0
      }
    ]);
  };

  //確認メッセージ後の処理
  React.useEffect(() => {
    //削除処理
    function doDel() {
      setIsLoading(true);
      console.log("Del : " + confirmInfo[2].id);
      setTimeout(() => {

        formatLstData();
        setMsgInfo([true, '削除成功しました。', 'success']);

      }, 1000);
    }

    //起動確認結果
    if (confirmInfo[2].opt === "del" && confirmInfo[2].result === 1) {
      doDel();
    }

  }, [confirmInfo]);




  return (
    <Box>
      <Grid container spacing={1}>
        {/* 検索部 */}
        <Grid item xs={6} sm={6} className={classes.searchBox}>
        </Grid>
        {/* 操作ボタン部 */}
        <Grid item xs={6} sm={6} className={classes.optBox}>
          <Button color="warning" onClick={() => handleClickStartAdd()}>アカウント追加</Button>
        </Grid>
        {/* ページジャンプ */}
        <Grid item xs={12} className={classes.pageJumpBox}>
          {/* [記録数、ページサイズ,当ページ番号] */}
          <NBrootsPagination
            count={pageInfos[0]}
            pagesize={pageInfos[1]}
            current={pageInfos[2]}
            onClick={handleChangePageStart}
            color="warning"
          />
        </Grid>
      </Grid>

      <Box xs={12} className={classes.tableBox}>
        <Paper className={classes.tableCardBody}>
          <TableContainer>

            <Table stickyHeader className={classes.table}>
              <TableHead>
                <TableRow>
                  <TableCell className={classes.tableHead} style={{ width: "20%" }}>メールアドレス</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "20%" }}>ユーザ名</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "20%" }}>フリガナ</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "15%" }}>管理者</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "25%" }}>操作</TableCell>
                </TableRow>
              </TableHead>
              <TableBody className={classes.tableBody}>
                {lstContent ? lstContent.map((item, key) =>
                  <TableRow className={classes.tableRow} key={key}>
                    <TableCell className={classes.tableCellLeft}>{item.email}</TableCell>
                    <TableCell className={classes.tableCellLeft}>{item.username}</TableCell>
                    <TableCell className={classes.tableCellLeft}>{item.katagana}</TableCell>
                    <TableCell className={classes.tableCell}>
                      {item.isadmin === 1 ? <FiberManualRecordIcon style={{ color: "#4AC18A" }} /> : null}
                    </TableCell>
                    <TableCell className={classes.tableCell} >
                      <Tooltip id="tooltip-top" title="詳細情報" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartEdit(item) }} color="warning" >
                          <Dvr />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="削除" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartDel(item) }} color="danger">
                          <Delete />
                        </Button>
                      </Tooltip>
                    </TableCell>
                  </TableRow>
                ) : null}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>


      </Box>
      {/* 編集Popup画面 */}
      <UserListDetailPopup info={userListDetailInfo} setInfo={setUserListDetailInfo} />
      {/* 追加Popup画面 */}
      <UserListAddPopup info={userListAddInfo} setInfo={setUserListAddInfo} />

      {/* 共通ダイアログ画面 */}
      <NBrootsConfirm info={confirmInfo} setInfo={setConfirmInfo} />
      <NBrootsDialogProcess open={isLoading} color="warning" />
      <NBrootsMsgBox open={msgInfo[0]} message={msgInfo[1]} color={msgInfo[2]} close setMessage={setMsgInfo} />
    </Box>
  );
}
