/* eslint-disable */
import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";


import Grid from '@material-ui/core/Grid';
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
// import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';
import Checkbox from "@material-ui/core/Checkbox";
import Check from "@material-ui/icons/Check";

// import FormControlLabel from "@material-ui/core/FormControlLabel";
// import Checkbox from "@material-ui/core/Checkbox";
// import Box from "@material-ui/core/Box";

// @material-ui/icons
import Add from "@material-ui/icons/Add";
import Close from "@material-ui/icons/Close";
import Delete from "@material-ui/icons/Delete";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import CustomInput from "components/CustomInput/CustomInput.js";

// exp components
import NBrootsPagination from "components/@NBrootsPagination/NBrootsPagination.js";
import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsMsgBox from "components/@NBrootsMsgBox/NBrootsMsgBox.js";
import NBrootsConfirm from "components/@NBrootsConfirm/NBrootsConfirm.js";

import { BASE_URL } from 'variables/config.js';
import Service from "utils/service/service.js"

import styles from "assets/jss/material-dashboard-pro-react/views/CommonPopup/CommonPopupStyle";

// メモリ中企業リスト
import { corpsInfo } from 'variables/runtimeInfos.js';
import { PAGINATION_PREV_NAME,PAGINATION_NEXT_NAME } from 'variables/config.js';

export default function PlayListPopup(props) {

  const useStyles = makeStyles(styles);

  const classes = useStyles();

  const { info, setInfo } = props;

  //進捗画面
  const [isLoading, setIsLoading] = React.useState(false);

  //Msg画面用情報             msgInfo     : [表示FLG, Msg, Type:"info","success","warning","danger","primary","rose"]    
  const [msgInfo, setMsgInfo] = React.useState([false, '', 'info']);
  //Confirm画面用情報         confirmInfo : [表示FLG, Msg, returnVal ]    
  const [confirmInfo, setConfirmInfo] = React.useState([false, '', { "opt": '', "id": '', "result": 0 }]);
  //検索条件
  var tSearch = {
    crop: '',
  }
  const [search, setSearch] = React.useState(tSearch);

  //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
  const [pageInfos, setPageInfos] = React.useState([0, 10, 1]);
  //明細情報
  const [lstContent, setLstContent] = React.useState([]);

  const open = info[0];

  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {
    //進捗画面
    setIsLoading(true);
    //情報のロード
    formatLstData();

  }, []);    //[] 忘れないよう…　初期化



  //APIJSON　→　画面用JSON（FLGやERRMSGなど）
  // const formatLstData = async () => {
  const formatLstData = () => {

    var url = BASE_URL + "/api/play_list.json";
    var data = {
    };

    Service(url, data, "GET").then(resp => {
      console.log(resp);
      // 正常系
      if (resp.status === 0) {
        var info = resp.infos;
        //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
        setPageInfos([info.count, info.pageSize, info.pageNo]);
        //Data情報の処理
        var lst = info.dataRows;
        lst.map(item => {
          return {
            ...item,
            //追加属性
          }
        });
        setLstContent(lst);
      } else {
        //その他APIエラー      
      }
      setIsLoading(false);
    }).catch(resp => {
      //接続エラーなど
      setIsLoading(false);
      setMsgInfo([true, resp, 'danger']);
    });
  };

  //検索条件部
  //企業変更の場合
  const handleChangeSearchStart = (e) => {
    console.log(e);
    let t = search;
    
    //選択情報をSTATEに保存
    if(e.target.name === 'corpSelect')
      t.crop = e.target.value;
    else if(e.target.name === 'groupSelect')
      t.group = e.target.value;
    setSearch(t);

    //検索ロジックを行う
    setIsLoading(true);

    setTimeout(() => {

      //情報のロード
      formatLstData(t);

      setIsLoading(false);

    }, 1500);

  }

  //改ページ処理
  // pageInfos   : [記録数、ページサイズ,当ページ番号]
  const handleChangePageStart = (page) => {

    if (page.disabled) return;
    if (page.text === pageInfos[2]) return;

    setIsLoading(true);

    setTimeout(() => {

      if (page.text === PAGINATION_PREV_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] - 1]);
      } else if (page.text === PAGINATION_NEXT_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] + 1]);
      } else {
        setPageInfos([pageInfos[0], pageInfos[1], page.text]);
      }

      setIsLoading(false);

    }, 500);
  }


  //保存ボタン押す
  const handleClickOKBtn = () => {
    console.log(corpsInfo);
    var tCorpListDetailInfo = [
      //当画面表示FLG    
      false,
      //当画面編集情報       
      {
        "id": "",
        "corp": "",
        "email": "",
        "username": "",
        "furikana": "",
        "password": "",
        "isadmin": 0
      },
      //IsAfterOK
      true
    ]
    setInfo(tCorpListDetailInfo);
  }

  //キャンセルボタン押す
  const handleClickCancelBtn = () => {
    var tCorpListDetailInfo = [
      //当画面表示FLG    
      false,
      //当画面編集情報       
      {

      },
      //IsAfterOK
      false
    ]
    setInfo(tCorpListDetailInfo);
  }

  return (

    <Dialog fullWidth={true} maxWidth={'lg'} onClose={handleClickCancelBtn} aria-labelledby="customized-dialog-title" open={open}>
      <DialogTitle id="customized-dialog-title" className={classes.dialogHeader} onClose={handleClickCancelBtn}>
          プレイリスト選択
        <Button
          justIcon
          className={classes.modalCloseButton}
          key="close"
          aria-label="Close"
          color="warning"
          onClick={handleClickCancelBtn}
          style={{ float: "right" }}
        >
          <Close className={classes.modalClose} />
        </Button>
      </DialogTitle>

      <DialogContent>
        <GridItem className={classes.modalDialog} xs={12} sm={12} md={12} lg={12}>
          <GridContainer>
            <Grid item xs={12} sm={4}>
            <FormControl style={{ width: "80%" }} className={classes.selectFormControl}>
                <InputLabel htmlFor="corpSelect" className={classes.selectLabel}>
                  企業
                </InputLabel>
                <Select
                  MenuProps={{ className: classes.selectMenu }}
                  classes={{ select: classes.select }}
                  onChange={(e) => handleChangeSearchStart(e)}
                  inputProps={{ name: "corpSelect"}}>
                  <MenuItem disabled classes={{ root: classes.selectMenuItem }}>
                    企業を選択
                  </MenuItem>
                  {corpsInfo ? corpsInfo.lst.map((item,key) =>
                    <MenuItem classes={{ root: classes.selectMenuItem, selected: classes.selectMenuItemSelected }} value={item.id} key={key}>
                      {item.corpname}
                    </MenuItem> 
                  ) : null}
                </Select>
              </FormControl>
              &nbsp;
              <br />
            </Grid>

            <Grid item xs={12} sm={8} className={classes.optBox}>
            </Grid>
            <Grid item xs={12} sm={12} className={classes.optBox}>
              {/* [記録数、ページサイズ,当ページ番号] */}
              <NBrootsPagination
                count={pageInfos[0]}
                pagesize={pageInfos[1]}
                current={pageInfos[2]}
                onClick={handleChangePageStart}
                color="warning"
              />
            </Grid>
            <TableContainer style={{ minHeight: 440, maxHeight: 440 }}>
              <Table stickyHeader>
                <TableHead>
                  <TableRow>
                    <TableCell className={classes.tableHead} style={{ width: "10%" }}></TableCell>
                    <TableCell className={classes.tableHead} style={{ width: "15%" }}>企業</TableCell>
                    <TableCell className={classes.tableHead} style={{ width: "45%" }}>プレイリスト名</TableCell>
                    {/* <TableCell className={classes.tableHead} style={{ width: "10%" }}>開始時間</TableCell>
                    <TableCell className={classes.tableHead} style={{ width: "10%" }}>終了時間</TableCell> */}
                    <TableCell className={classes.tableHead} style={{ width: "10%" }}>サイズ</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody className={classes.tableBody}>
                  {lstContent ? lstContent.map((item, key) =>
                    <TableRow className={classes.tableRow} key={key}>
                      <TableCell className={classes.tableCell}>
                        <Checkbox
                          tabIndex={-1}
                          checkedIcon={<Check className={classes.checkedIcon} />}
                          icon={<Check className={classes.uncheckedIcon} />}
                          classes={{
                            checked: classes.checked,
                            root: classes.checkRoot
                          }}
                        />
                      </TableCell>
                      <TableCell className={classes.tableCell}>{item.corpname}</TableCell>
                      <TableCell className={classes.tableCell}>{item.playlistname}</TableCell>
                      {/* <TableCell className={classes.tableCell}>{item.startTime}</TableCell>
                      <TableCell className={classes.tableCell}>{item.endTime}</TableCell> */}
                      <TableCell className={classes.tableCell}>{item.size}</TableCell>
                    </TableRow>
                  ) : null}
                </TableBody>
              </Table>
            </TableContainer>
          </GridContainer>
        </GridItem>

      </DialogContent>

      <DialogActions>
        <Button onClick={handleClickCancelBtn} color="warning">
          キャンセル
          </Button>
        <Button onClick={handleClickOKBtn} color="warning">
          選択
        </Button>
      </DialogActions>
        {/* 共通ダイアログ画面 */}
        <NBrootsConfirm info={confirmInfo} setInfo={setConfirmInfo} />
        <NBrootsDialogProcess open={isLoading} color="warning" />
        {/* <NBrootsMsgBox open={msgInfo[0]} message={msgInfo[1]} color={msgInfo[2]} close setMessage={setMsgInfo} /> */}
    </Dialog>

  );
}
