import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import InputLabel from "@material-ui/core/InputLabel";
import TableContainer from '@material-ui/core/TableContainer';

// @material-ui/icons
import Add from "@material-ui/icons/Add";
import Delete from "@material-ui/icons/Delete";

// core components
import Tooltip from "@material-ui/core/Tooltip";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardText from "components/Card/CardText.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';

// exp components
// import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsMsgBox from "components/@NBrootsMsgBox/NBrootsMsgBox.js";

import PlayListPopup from "views/CommonPopup/PlayListPopup.js";

import styles from "assets/jss/material-dashboard-pro-react/views/TerminalList/terminalDetailStyle.js";

// メモリ中企業リスト
import { corpsInfo, groupsInfo } from 'variables/runtimeInfos.js';


export default function TerminalAdd(props) {
  //样式
  const useStyles = makeStyles(styles);
  const classes = useStyles();

  //進捗画面
  // const [isLoading, setIsLoading] = React.useState(false);
  //Msg画面用情報             msgInfo     : [表示FLG, Msg, Type:"info","success","warning","danger","primary","rose"]    
  const [msgInfo, setMsgInfo] = React.useState([false, '', 'info']);

  const [playListInfo, setPlayListInfo] = React.useState([false, '', false]);

  //明細情報
  const [lstPlayListContent, setLstPlayListContent] = React.useState([]);


  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {


  }, []);    //[] 忘れないよう…　初期化


  //PlayList追加ボタン押す
  const handleClickAddPlaylistDialogOpen = () => {
    var tPlayListInfo = [
      true,          //Popup画面表示FLG
      {               //情報
      },
      false           //保存ボタン押すFLG
    ]
    setPlayListInfo(tPlayListInfo);
  };


  //画面動作 DEL
  const handleClickPlayListStartDel = (obj) => {

    var index = lstPlayListContent.indexOf(obj);

    // setIsLoading(true);

    setTimeout(() => {

      lstPlayListContent.splice(index, 1);
      //！！！　Array　展開方式 ！！！
      setLstPlayListContent([...lstPlayListContent]);

      // setIsLoading(false);

    }, 1000);


  };



  return (
    <Box>
      <Grid container spacing={1} >
        <GridItem xs={12} sm={6}>
          <Card>
            <CardHeader>
              <CardText color="primary">
                <h4 className={classes.cardTitle}>設置・表示情報</h4>
              </CardText>
            </CardHeader>
            <CardBody>
              <Grid container spacing={1}>
                <Grid item xs={6} sm={6}>
                  <FormControl style={{ width: "90%" }} className={classes.selectFormControl}>
                    <InputLabel htmlFor="simple-select" className={classes.selectLabel}>
                      企業
                    </InputLabel>
                    <Select
                      MenuProps={{ className: classes.selectMenu }}
                      classes={{ select: classes.select }}
                      // value={simpleSelect}
                      // onChange={handleSimple}
                      inputProps={{ name: "corpSelect", id: "corpSelect" }}>
                      <MenuItem disabled classes={{ root: classes.selectMenuItem }}>
                        企業を選択
                      </MenuItem>
                      {corpsInfo ? corpsInfo.lst.map((item, key) =>
                        <MenuItem classes={{ root: classes.selectMenuItem, selected: classes.selectMenuItemSelected }} value={item.id} key={key}>
                          {item.corpname}
                        </MenuItem> 
                      ) : null}

                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={6} sm={6}>
                  <FormControl style={{ width: "90%" }} className={classes.selectFormControl}>
                    <InputLabel htmlFor="groupSelect" className={classes.selectLabel}>
                      設置場所名
                    </InputLabel>
                    <Select
                      MenuProps={{ className: classes.selectMenu }}
                      classes={{ select: classes.select }}
                      // value={simpleSelect}
                      // onChange={handleSimple}
                      inputProps={{ name: "groupSelect", id: "groupSelect" }}>
                      <MenuItem disabled classes={{ root: classes.selectMenuItem }}>
                        場所名を選択
                      </MenuItem>
                      {groupsInfo ? groupsInfo.lst.map((item,key) =>
                        <MenuItem classes={{ root: classes.selectMenuItem, selected: classes.selectMenuItemSelected }} value={item.id} key={key}>
                          {item.group}
                        </MenuItem> 
                      ) : null}

                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={12} className={classes.searchBox}>
                  <CustomInput
                    style={{ width: "40%" }}
                    labelText="場所説明 *"
                    id="position"
                    formControlProps={{ fullWidth: true }}
                    inputProps={{ type: "text", autoComplete: "off" }}
                  />
                </Grid>



              </Grid>





            </CardBody>
          </Card>
          <Card>
            <CardHeader>
              <CardText color="primary">
                <h4 className={classes.cardTitle}>プレイリスト設定</h4>
              </CardText>
            </CardHeader>
            <CardBody>
              <Grid container spacing={1}>
                {/* 検索部 */}
                <Grid item xs={6} sm={6} className={classes.searchBox}>
                  {"TODO：連携端末：複数端末詳細＜TAB＞"}
                </Grid>
                {/* 操作ボタン部 */}
                <Grid item xs={6} sm={6} className={classes.optBox}>
                  <Button color="warning" onClick={handleClickAddPlaylistDialogOpen}>
                    <Add className={classes.icons} />プレイリスト追加
                    </Button>
                </Grid>
              </Grid>
              <TableContainer style={{ minHeight: 370, maxHeight: 370 }}>
                <Table stickyHeader>
                  <TableHead>
                    <TableRow>
                      {/* <TableCell className={classes.tableHead} style={{ width: "15%" }}>開始時間</TableCell>
                      <TableCell className={classes.tableHead} style={{ width: "15%" }}>終了時間</TableCell> */}
                      <TableCell className={classes.tableHead} style={{ width: "50%" }}>プレイリスト</TableCell>
                      <TableCell className={classes.tableHead} style={{ width: "20%" }}>操作</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    {lstPlayListContent ? lstPlayListContent.map((item, key) =>
                      <TableRow className={classes.tableRow} key={key}>
                        {/* <TableCell className={classes.tableCell} >{item.terminalStart}</TableCell>
                        <TableCell className={classes.tableCell} >{item.terminalEnd}</TableCell> */}
                        <TableCell className={classes.tableCellLeft} >{item.playlist}</TableCell>
                        <TableCell className={classes.tableCell} >
                          <Tooltip id="tooltip-top" title="削除" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                            <Button simple className={classes.actionButton} onClick={() => { handleClickPlayListStartDel(item) }} color="danger">
                              <Delete />
                            </Button>
                          </Tooltip>
                        </TableCell>
                      </TableRow>
                    ) : null}
                  </TableBody>
                </Table>
              </TableContainer>
            </CardBody>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6}>

          <Card>
            <CardHeader>
              <CardText color="primary">
                <h4 className={classes.cardTitle}>端末詳細</h4>
              </CardText>
            </CardHeader>
            <CardBody>
              <Grid container spacing={2}>
                <Grid item xs={6} sm={6} className={classes.searchBox}>
                  <CustomInput
                    labelText="機種 *"
                    id="office"
                    formControlProps={{ fullWidth: true }}
                    inputProps={{ type: "text", autoComplete: "off" }}
                  />
                </Grid>
                <Grid item xs={6} sm={6} className={classes.searchBox}>
                </Grid>
                <Grid item xs={6} sm={6} className={classes.searchBox}>
                  <CustomInput
                    labelText="端末番号 *"
                    id="terminalNum"
                    formControlProps={{ fullWidth: true }}
                    inputProps={{ type: "text", autoComplete: "off" }}
                  />
                </Grid>

                <Grid item xs={6} sm={6} className={classes.searchBox}>

                  <CustomInput
                    labelText="SSID *"
                    id="age"
                    formControlProps={{ fullWidth: true }}
                    inputProps={{ type: "text", autoComplete: "off" }}
                  />
                </Grid>

              </Grid>
            </CardBody>
          </Card>
          <Box style={{ textAlign: "right" }}>
            <Button color="warning">
              &nbsp;&nbsp;保&nbsp;存&nbsp;&nbsp;
            </Button>
          </Box>
        </GridItem>
      </Grid>
      <PlayListPopup info={playListInfo} setInfo={setPlayListInfo} />
      {/* 共通ダイアログ画面 */}
      {/* <NBrootsDialogProcess open={isLoading} color="warning" /> */}
      <NBrootsMsgBox open={msgInfo[0]} message={msgInfo[1]} color={msgInfo[2]} close setMessage={setMsgInfo} />
    </Box>
  );
}
