/* eslint-disable */
import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";

// core components
import Button from "components/CustomButtons/Button.js";
import NBrootsDialogPlayer from "components/@NBrootsDialogPlayer/NBrootsDialogPlayer.js";

// @material-ui/icons
import Dvr from "@material-ui/icons/Dvr";
import Delete from "@material-ui/icons/Delete";
import PlayCircleOutline from "@material-ui/icons/PlayCircleOutline";
// exp components
import NBrootsPagination from "components/@NBrootsPagination/NBrootsPagination.js";
import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsMsgBox from "components/@NBrootsMsgBox/NBrootsMsgBox.js";
import NBrootsConfirm from "components/@NBrootsConfirm/NBrootsConfirm.js";

import Service from "utils/service/service.js"

import { BASE_URL } from 'variables/config.js';

import styles from "assets/jss/material-dashboard-pro-react/views/PlayList/playListStyle";

// メモリ中企業リスト
import { corpsInfo } from 'variables/runtimeInfos.js';
import { PAGINATION_PREV_NAME,PAGINATION_NEXT_NAME } from 'variables/config.js';

export default function PlayList(props) {

  const useStyles = makeStyles(styles);

  const classes = useStyles();
  
  //進捗画面
  const [isLoading, setIsLoading] = React.useState(false);
  //Msg画面用情報             msgInfo     : [表示FLG, Msg, Type:"info","success","warning","danger","primary","rose"]    
  const [msgInfo, setMsgInfo] = React.useState([false, '', 'info']);
  //Confirm画面用情報         confirmInfo : [表示FLG, Msg, returnVal ]    
  const [confirmInfo, setConfirmInfo] = React.useState([false, '', { "opt": '', "id": '', "result": 0 }]);

  //player画面用情報          playerInfo  : [表示FLG、VedioSrc]    
  const [playerInfo, setPlayerInfo] = React.useState([false, '']);

  var tSearch = {
    crop: ''
  }
  const [search, setSearch] = React.useState(tSearch);
  //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
  const [pageInfos, setPageInfos] = React.useState([0, 10, 1]);
  //明細情報
  const [lstContent, setLstContent] = React.useState([]);


  //初期化処理
  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {

    //進捗画面
    setIsLoading(true);
    //情報のロード
    formatLstData();

  }, []);    //[] 忘れないよう…　初期化

  //APIJSON　→　画面用JSON（FLGやERRMSGなど）
  // const formatLstData = async () => {
  const formatLstData = () => {


    var url = BASE_URL + "/api/play_list.json";

    var data = {

    };

    Service(url, data, "GET").then(resp => {
      console.log(resp);
      // 正常系
      if (resp.status === 0) {

        var info = resp.infos;

        //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
        setPageInfos([info.count, info.pageSize, info.pageNo]);

        //Data情報の処理
        var lst = info.dataRows;
        lst.map(item => {
          return {
            ...item,
            //追加属性
            isEdit: false,
            chkResultName: false,
          }
        });

        setLstContent(lst);
      } else {
        //その他APIエラー      
      }

      setIsLoading(false);
    }).catch(resp => {
      //接続エラーなど
      // console.log(resp);

      setIsLoading(false);

      setMsgInfo([true, resp, 'danger']);

    });

  };


  //検索条件部
  //企業変更の場合
  const handleChangeSearchStart = (e) => {
    console.log(e);
    let t = search;
    
    //選択情報をSTATEに保存
    if(e.target.name === 'corpSelect')
      t.crop = e.target.value;
    // else if(e.target.name === 'groupSelect')
    //   t.group = e.target.value;
    setSearch(t);

    //検索ロジックを行う
    setIsLoading(true);

    setTimeout(() => {

      //情報のロード
      formatLstData(t);

      setIsLoading(false);

    }, 1500);

  }


  //改ページ処理
  // pageInfos   : [記録数、ページサイズ,当ページ番号]
  const handleChangePageStart = (page) => {

    if (page.disabled) return;
    if (page.text === pageInfos[2]) return;

    setIsLoading(true);

    setTimeout(() => {

      if (page.text === PAGINATION_PREV_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] - 1]);
      } else if (page.text === PAGINATION_NEXT_NAME) {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] + 1]);
      } else {
        setPageInfos([pageInfos[0], pageInfos[1], page.text]);
      }

      setIsLoading(false);

    }, 500);
  }

  //再生ボタン押す
  const handleClickStartPlay = (obj) => {
    //Player画面を開く
    setPlayerInfo([true, 'https://akechi1980.github.io/NBroots/img/VedioMarket/1.mp4']);

  }

  //変更ボタン押す
  const handleClickStartEdit = (obj) => {
    props.history.push('/PlayList/Detail/' + obj.id);
  };

  //追加ボタン押す
  const handleClickStartAdd = (obj) => {
    props.history.push('/PlayList/Add');
  };

  //削除ボタン押す  
  const handleClickStartDel = (obj) => {
    var index = lstContent.indexOf(obj);

    setConfirmInfo([true, "削除、よろしいでしょうか？",
      {
        "opt": "del",
        "id": index,
        "result": 0
      }
    ]);
  };



  //確認メッセージ後の処理
  React.useEffect(() => {

    //削除処理
    function doDel(){
      setIsLoading(true);
      console.log("Del : " + confirmInfo[2].id);
      setTimeout(() => {

        formatLstData();
        setMsgInfo([true, '削除成功しました。', 'success']);

      }, 1000);
    }

    //起動確認結果
    if (confirmInfo[2].opt === "del" && confirmInfo[2].result === 1) {
      doDel();
    }

  }, [confirmInfo]);

  return (
    <Box>
      <Grid container spacing={1}>
        {/* 検索部 */}
        <Grid item xs={6} sm={6} className={classes.searchBox}>
          <FormControl style={{ width: "40%" }} className={classes.selectFormControl}>
            <InputLabel htmlFor="corpSelect" className={classes.selectLabel}>
              企業
            </InputLabel>
            <Select
              MenuProps={{ className: classes.selectMenu }}
              classes={{ select: classes.select }}
              value={search.crop}
              onChange={(e) => handleChangeSearchStart(e)}
              inputProps={{ name: "corpSelect"}}>
              <MenuItem disabled classes={{ root: classes.selectMenuItem }}>
                企業を選択
              </MenuItem>
              {corpsInfo ? corpsInfo.lst.map((item,key) =>
                <MenuItem classes={{ root: classes.selectMenuItem, selected: classes.selectMenuItemSelected }} value={item.id} key={key}>
                  {item.corpname}
                </MenuItem> 
              ) : null}

            </Select>
          </FormControl>
        </Grid>
        {/* 操作ボタン部 */}
        <Grid item xs={6} sm={6} className={classes.optBox}>
          <Button color="warning" onClick={() => handleClickStartAdd()}>プレイリスト追加</Button>
        </Grid>
        {/* ページジャンプ */}
        <Grid item xs={12} className={classes.pageJumpBox}>
          {/* [記録数、ページサイズ,当ページ番号] */}
          <NBrootsPagination
            count={pageInfos[0]}
            pagesize={pageInfos[1]}
            current={pageInfos[2]}
            onClick={handleChangePageStart}
            color="warning"
          />
        </Grid>
      </Grid>

      <Box xs={12} className={classes.tableBox}>
        <Paper className={classes.tableCardBody}>
          <TableContainer>
            <Table stickyHeader className={classes.table}>
              <TableHead>
                <TableRow>
                  <TableCell className={classes.tableHead} style={{ width: "15%" }}>企業</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "35%" }}>プレイリスト名</TableCell>
                  {/* <TableCell className={classes.tableHead} style={{ width: "10%" }}>開始時間</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "10%" }}>終了時間</TableCell> */}
                  <TableCell className={classes.tableHead} style={{ width: "10%" }}>サイズ</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "20%" }}>操作</TableCell>
                </TableRow>
              </TableHead>
              <TableBody className={classes.tableBody}>
                {lstContent ? lstContent.map((item, key) =>
                  <TableRow className={classes.tableRow} key={key}>
                    <TableCell className={classes.tableCellLeft} >{item.corpname}</TableCell>
                    <TableCell className={classes.tableCellLeft} >{item.playlistname}</TableCell>
                    {/* <TableCell className={classes.tableCell} >{item.startTime}</TableCell>
                    <TableCell className={classes.tableCell} >{item.endTime}</TableCell> */}
                    <TableCell className={classes.tableCell} >
                      {item.size}
                    </TableCell>
                    <TableCell className={classes.tableCell} >

                      <Tooltip id="tooltip-top" title="再生" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartPlay(item) }} color="transparent" >
                          <PlayCircleOutline />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="詳細情報" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartEdit(item) }} color="warning" >
                          <Dvr />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="削除" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartDel(item) }} color="danger">
                          <Delete />
                        </Button>
                      </Tooltip>
                    </TableCell>
                  </TableRow>
                ) : null}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>


      </Box>

      {/* 共通ダイアログ画面 */}
      <NBrootsConfirm info={confirmInfo} setInfo={setConfirmInfo} />
      <NBrootsDialogPlayer open={playerInfo[0]} src={playerInfo[1]} onClose={() => setPlayerInfo([false, ''])} />
      <NBrootsDialogProcess open={isLoading} color="warning" />
      <NBrootsMsgBox open={msgInfo[0]} message={msgInfo[1]} color={msgInfo[2]} close setMessage={setMsgInfo} />
    </Box>
  );
}
