import Service from "utils/service/service.js"
import { BASE_URL } from 'variables/config.js';

// メモリ中企業リスト
import { corpsInfo, groupsInfo } from 'variables/runtimeInfos.js';

//画面遷移する時、処理を行う
export const initSystemData = () => {

  //ログインユーザ情報の取得
  initUserData();

  //企業リストの取得
  initCorpListData();

  //設置場所リストの取得
  getGroupListData(1);

};

const initUserData = () => {
  
  console.log('ログインユーザ情報の取得');

};


const initCorpListData = () => {

  if(corpsInfo.lst.length > 0) return ;

  console.log('企業リストの取得');

  var url = BASE_URL + "/api/corp_list.json";

  var data = {

  };

  Service(url, data, "GET").then(resp => {
    console.log(resp);
    // 正常系
    if (resp.status === 0) {

      var info = resp.infos;
      //Data情報の処理
      var lst = info.dataRows;
      //メモリにセットする。
      corpsInfo.lst = lst;

    } else {
      //その他APIエラー      
    }
  }).catch(resp => {
    //接続エラーなど

  });

};


const getGroupListData = (corpid) => {

  if(groupsInfo.lst.length > 0) return ;

  console.log('設置場所リストの取得');

  var url = BASE_URL + "/api/group_list.json";

  var data = {
    corpId : corpid
  };

  Service(url, data, "GET").then(resp => {
    console.log(resp);
    // 正常系
    if (resp.status === 0) {

      var info = resp.infos;
      //Data情報の処理
      var lst = info.dataRows;
      //メモリにセットする。
      groupsInfo.lst = lst;

    } else {
      //その他APIエラー      
    }
  }).catch(resp => {
    //接続エラーなど

  });

};