import {
  REQUEST_TIMEOUT,
  ERROR_TIMEOUT,
  // ERROR_CONFLICT,
  // ERROR_UNAUTHORIZED,
  // ERROR_BAD_REQUEST,
} from 'variables/config.js';


const createOptions = (method, data, token) => ({
  method,
  headers: new Headers({
    'Content-Type': 'application/json',
    'x-pmscms-accesstoken': 'Bearer ' + token
  }),
  body: JSON.stringify(data),
});

const createGetOptions = (method, data, token) => ({
  method,
  headers: new Headers({
    'Content-Type': 'application/json',
    'x-pmscms-accesstoken': 'Bearer ' + token
  }),
});



const createError = status => ({ status });

export default function Service(url, data, method = 'POST', timeout = REQUEST_TIMEOUT) {

  const token = localStorage.getItem('token');

  var options = "";
  
  if(method === 'GET'){
    options = createGetOptions(method, token);
  }else{
    options = createOptions(method, data, token);
  }



  return new Promise(async (resolve, reject) => {

    let timer = setTimeout(() => {
      return reject(createError(ERROR_TIMEOUT));
    }, timeout);

    //TOKEN 暂时删除
    // if (token === null) {
    //   return reject(createError(ERROR_UNAUTHORIZED));
    // }

    const response = await fetch(url, options);
    clearTimeout(timer);

    const json = await response.json();

    if (response.ok) {
      return resolve(json);
    }

    return reject(response);
  });
}
